export default {
	data() {
		return {
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [
				{
					latitude: 39.909,
					longitude: 116.39742,
					iconPath: '../../../static/location.png'
				},
				{
					latitude: 39.9,
					longitude: 116.39,
					iconPath: '../../../static/location.png'
				}
			],
			id:0
		};
	},
	methods: {
		toPage() {
			let obj = new Object();
			obj.name = 'category';
			let param = new Object();
			param.id = '12341';
			obj.query = param;
			this.$openPage(obj);
		},
		//调用方式一
		testRequest1() {
			this.$minApi
				.uniapp('app/common/18317116346/code', {})
				.then(res => {
					this.res = res;
					console.log(res);
				})
				.catch(err => {
					console.log(err);
				});
		},
		//调用方式二
		async testReqeust2() {
			try {
				const res = await this.$minApi.uniapp('app/common/18317116346/code', {});
				console.log(res);
			} catch (e) {
				console.log('异常', e);
			}
		},

		testSetCache() {
			const value = this.$cache.set('name', 'wangshuai');
			console.log('存入的值{}', value);
		},

		testGetCache() {
			const value = this.$cache.get('name');
			console.log('获取缓存的值{}', value);
		}
	},

	onLoad() {
		// this.testRequest1();
		this.testReqeust2();
		this.testSetCache();
		this.testGetCache();
		//调用工具类方法测试
		console.log('this BaseUtils', this.isBlank('dsafasdfasdfa'));
	}
};
